<template>
    <!-- 登录 -->
    <div id="login">
        <div class="login-form">
            <div class="login-inner">
                <h2>
                    <p>准考证打印</p>
                    <p class="f14">PRINT</p>
                </h2>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm"
                    label-width="100px"
                    class="demo-ruleForm">
                    <el-form-item label="身份证号" prop="cardNum">
                        <el-input v-model="ruleForm.cardNum"></el-input>
                    </el-form-item>
                    <el-form-item label="考次" prop="examId">
                        <el-select v-model="ruleForm.examId" placeholder="请选择考次">
                            <el-option v-for="item in examIdOptions"
                                :key="item.id" 
                                :label="item.name"
                                :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <div class="down-btn" @click="setTemp">
                            <a class="downExcel" :href="url">下载准考证</a>
                        </div>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import baseurl from '@/axios/base.js'
export default {
    data(){
        return{
            url:'',
            ruleForm:{
                cardNum:'',
                examId:''
            },
            rules:{
                cardNum:[
                { required:true,message:'请输入身份证号码',trigger:'blur'}
                ],
                examId:[
                { required:true,message:'请选择考次',trigger:'blur'},
                ]
            },
            examIdOptions:[]
        }
    },
    methods:{
        setTemp(){
            if(!this.ruleForm.cardNum){
                return this.$message.error('请填写身份证号')
            }
            if(!this.ruleForm.examId){
                return this.$message.error('请选择考次号');
            }
            this.url= baseurl.url+`/pubic/admission/download?cardNum=${this.ruleForm.cardNum}&examId=${this.ruleForm.examId}`;
        },
        getexamIdOptions(){//获取考次
            this.$api.examlist().then(res=>{
                this.examIdOptions=res.data.exams;
            })
        }
    },
    created(){
        this.getexamIdOptions();
    }
}
</script>

<style lang="scss" scoped>
    #login{
        padding: 50px 0;
        background: #f5f5f5;

        .login-form{
            margin: auto;
            width: 520px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;
            padding: 10px;
            background: #fff;
            
            .login-inner{
              width: 100%;
              height: 100%;
              border: 1.2px solid #4579c7;
              padding: 20px 26px;
              box-sizing: border-box;

              .el-button{
                width: 100%;
              }
              h2{
                font-weight: 500;
                border-bottom: 1px dashed #333;
                text-align: left;
                p.f14{
                  font-weight: 800;
                  font-size: 14px;
                  color: #d5d5d5;
                }
              }
            }
            .el-form{
              margin-top: 10px;
            }           
        }
    }
    .down-btn{
        text-align: center;
        box-sizing: border-box;
        border-radius: 5px;
        background: #45a1ff;
        margin: 0 5px;

        a{
            color: #fff;
        }

        &:hover{
            cursor: pointer;
        }
    }
</style>